/** header **/
.header {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  height: auto;
  padding-bottom: 0px;
}

.mobilegroup {
  margin: 0.7em auto 0.7em 0.7em;
}

.navmenu {
  list-style: none;
  align-self: flex-start;
}

.navmenu > * {
  display: inline-block;
  padding: 0.3em 20px 0.3em;
}

/** Responsive header **/
.hamburger {
  font-size: 30px;
  color: white;
  margin: 1em;
  cursor: pointer;
  display: none;
}

#hamburger--check {
  display: none;
}

@media (max-width: 1100px) {
  .header {
    display: block;
    padding-bottom: 0px;
    z-index: 9;
  }

  .mobilegroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0px;
    padding: 0px 0.5em 0px;
  }

  .hamburger {
    display: inline-block;
    margin: 0.4em;
  }

  .navmenu {
    position: fixed;
    width: 100%;
    height: 100vh;
    background: azure;
    text-align: center;
    padding: 0px;
    left: 100%;
    transition: all 0.2s;
  }

  .navmenu > * {
    display: block;
    margin: 0 1em;
    padding: 1em;
    border-bottom: 1px solid rgba(91, 147, 194, 0.5);
  }

  #hamburger--check:checked + .navmenu {
    left: 0%;
  }
}

/** Overrides **/
h1,
ul {
  margin: 0px;
}

.logo {
  margin: 0px;
}

.logo h1 {
  font-weight: 300;
  text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.3);
  transition: text-shadow 0.18s ease;

  &:hover {
    text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.8);
  }
}

.navmenu a {
  color: #244067;
  font-weight: 400;
  font-size: 21px;
}

/** trapezoid **/
.trapezoid {
  background-color: white;
  color: white;
  display: inline-block;
  max-width: 250px;
  position: relative;
  line-height: 2em;
  padding: 0 0.5em;
  margin: 0 0em 0em 2em;
  vertical-align: top;
  min-width: 6em;
}

.trapezoid:before {
  content: "";
  background: inherit;
  width: 2em;
  height: 100%;
  -webkit-clip-path: polygon(50% 0%, 100% 0, 100% 100%, 0% 100%);
  clip-path: polygon(0% 0%, 100% 0, 100% 100%, 60% 100%);
  position: absolute;
  right: 100%;
  top: 0;
  border: 0;
  margin-right: -1px;
}
